                    <li>Name area can be max 100 signs and  min 5 signs #TODO</li>
                </div>
            </div>
        </div>
        <div class="line-form" v-if="driver=='Snap7'">
            <div class="name-line">Type of area:</div>
            <div class="input-form">
                <el-select v-model="areaParam.type_area" class="m-2" placeholder="Select type" size="small" namespace="hrad-1" popper-class="hrad-2"
                    :change="$emit('onBigType', (areaParam.type_area == 'DB'))"
                >
