                </div>
            </div>
        </div>
        <Transition name="fade">
            <div class="line-form try" v-if="areaParam.type_area || areaParam.function" @click="saveMethod">
                <div class="add-area-button">
                    <div class="icon-button">
                        <img  src="@/assets/img/ptich.svg">
                    </div>
                    <p class="name-button" v-if="mode_change">Change</p>
                    <p class="name-button" v-if="!mode_change">Save</p>
                </div>
            </div>
        </Transition>
    </div>
</template>

<script>
// import SelectElement from './SelectElement.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
    props: [
        'mode_change', 'index_area', 'connection_id', 'driver'
    ],
    components: {
        // SelectElement,
    },
    emits: ['closeEnd', 'onBigType'],
    data() {
        return {
            areaParam: {
                name: '',
                number_db: null,
                start_byte: null,
                offset: null,
                connection: ''
            },
            drivers: [
                {
                    key: 1,
                    label: 'DB',
                    value: 'DB'
                },
                {
                    key: 2,
                    label: 'PA',
                    value: 'PA'
                }
            ],
            function_kind: [
                {
                    key: 1,
                    label: '3',
                    value: 3
                },
                {
                    key: 2,
                    label: '4',
                    value: 4
                }
            ]
        }
    },
    methods: {
        async saveMethod() {
            // let validStatus = this.checkParam() #TODO
            if (!this.mode_change) {
                this.areaParam.connection = 'http://127.0.0.1:8000/connection/'+this.connection_id+'/'
                await this.addNewArea(this.areaParam)
                this.areaParam.connection = ''
                this.$emit('closeEnd');

            } else {
                this.areaParam.connection = 'http://127.0.0.1:8000/connection/'+this.connection_id+'/'
                this.changeArea({
                    id_area: this.areaList[this.index_area].id,
                    data: this.areaParam
                })
                this.areaParam.connection = ''
                this.$emit('closeEnd');
            }
            
        },
        ...mapActions({
            deleteArea: 'deleteArea',
            addNewArea: 'addNewArea',
            changeArea: 'changeArea'
        }),
        checkParam() {
            let answer = false
            if ('string' == typeof this.areaParam.name && this.areaParam.name.split(' ').length == 1) {
                
                if (this.areaParam.type_area == 'DB' && this.areaParam.number_db) {
                    if (this.areaParam.start_byte && this.areaParam.offset) {
                        answer = true
                    }
                } else if (this.areaParam.type_area == 'PA') {
                    if (this.areaParam.start_byte && this.areaParam.offset) {
                        answer = true
                    }
                }
            }
            return answer
        }
    },
    computed: {
        ...mapGetters({
            areaList: 'getAreaList'
        })
    },
    mounted() {
        if (this.mode_change) {
            this.areaParam.name = this.areaList[this.index_area].name
            this.areaParam.type_area = this.areaList[this.index_area].type_area
            this.areaParam.number_db = this.areaList[this.index_area].number_db
            this.areaParam.start_byte = this.areaList[this.index_area].start_byte
            this.areaParam.offset = this.areaList[this.index_area].offset
            this.areaParam.slave_id = this.areaList[this.index_area].slave_id
            this.areaParam.function = this.areaList[this.index_area].function
            this.areaParam.size = this.areaList[this.index_area].size
            this.areaParam.start_register = this.areaList[this.index_area].start_register
        }
    },
}
</script>

