            <li>Write data if data change</li>
        </div>
    </div>
</div>
<div class="last-line">
    <button class="button-submit" @click="saveVariable">
        <div class="icon-button">
            <img  src="@/assets/img/ptich.svg">
        </div>
        <p class="name-button" v-if="!changeMode">Save</p>
        <p class="name-button" v-if="changeMode">Change</p>
    </button>
</div>
</template>

<script>
import SelectElement from './SelectElement.vue';
import { mapActions } from 'vuex';

export default {
    components: {
        SelectElement,
    },
    props: ['area_id', 'changeMode', 'variableParam', 'driver'],
    emits: ['closeForm'],
    data() {
        return {
            paramVariable: {},
            kindOfType: [
                {
                    key: 1,
                    label: 'int',
                    value: 'int'
                },
                {
                    key: 2,
                    label: 'float',
                    value: 'float'
                },
                {
                    key: 3,
                    label: 'double',
                    value: 'double'
                },
                {
                    key: 4,
                    label: 'bool',
                    value: 'bool'
                }
            ]
        }
    },
    methods: {
        ...mapActions({
            addNewVareable: 'addNewVareable',
            changeVareable: 'changeVareable'
        }),
        saveVariable() {
            let paramVariable = {
                name: this.paramVariable.name,
                start: this.paramVariable.start,
                type_value_get_data: this.paramVariable.type_value.value,
                type_value_write_data: this.paramVariable.type_value_for_write.value,
                if_change: this.paramVariable.if_change,
                divide: this.paramVariable.divide,
                divide_number: this.paramVariable.divide_namber,
                time_write_if_change: this.paramVariable.time_min,
                bit: this.paramVariable.bit,
                byte_swap: this.paramVariable.byte_swap,
                big_endian: this.paramVariable.big_endian,
                signed: this.paramVariable.signed,
                area: 'http://127.0.0.1:8000/area/'+ this.area_id +'/'
            }
            if (!this.changeMode) {
                this.addNewVareable(paramVariable)
            } else {
                this.changeVareable({
                    id: this.variableParam.id,
                    data: paramVariable
                })
                this.$emit('closeForm')
            }
        }
    },
    mounted() {
        if (this.changeMode) {
            this.paramVariable.name = this.variableParam.name
            this.paramVariable.start = this.variableParam.start
            this.kindOfType.find(el => {
                if (el.value==this.variableParam.type_value_get_data) {
                    this.paramVariable.type_value = el
                }
            })
            this.kindOfType.find(el => {
                if (el.value==this.variableParam.type_value_write_data) {
                    this.paramVariable.type_value_for_write = el
                }
            })
            this.paramVariable.if_change = this.variableParam.if_change
            this.paramVariable.divide = this.variableParam.divide
            this.paramVariable.divide_namber = this.variableParam.divide_number
            this.paramVariable.time_min = this.variableParam.time_write_if_change
            this.paramVariable.bit = this.variableParam.bit
            this.paramVariable.byte_swap = this.variableParam.byte_swap
            this.paramVariable.big_endian = this.variableParam.big_endian
            this.paramVariable.signed = this.variableParam.signed

        }
    }
}
</script>

