<script>
import { mapMutations, mapActions, mapGetters } from 'vuex'
import LoaderIcon from './LoaderIcon.vue';

export default {
    name: 'WindowCreateConnection',
    components: {
        LoaderIcon
    },
    computed: {
        ...mapGetters({
            procReques: 'getloadConnection'
        })
    },
    data() {
        return {
            connectionParam: {
                name: '',
                ip_address: '',
                port: null,
                driver: '',
                slot: null,
                rack: null
            },
            drivers: [
                {
                    key: 1,
                    label: 'Snap7',
                    value: 'Snap7'
                },
                {
                    key: 2,
                    label: 'ModbusTCP',
                    value: 'ModbusTCP'
                }
            ]
        }
    },
    methods: {
        ... mapActions({
            addConnection: 'addConnection'
        }),
        ...mapMutations({
            CloseWindow: 'setWindowCreateConnection'
        }),
        createConnection() {
            this.addConnection(this.connectionParam)
        }
    }
}
</script>

